import React from "react";
import { Link, NavLink } from 'react-router-dom';

export function Navbar(props) {
  return <>
  <nav className="navbar navbar-expand-lg navbar-light bg-dark">
  <div className="container-fluid">
    <a className="navbar-brand text-white" href="#">Navbar</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent ">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0 ">
        <li className="nav-item">
        <NavLink className="nav-link text-white" to="/home">Home</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link text-white" to="/about">AboutUs</NavLink>
        </li>
        <li className="nav-item">
         <NavLink className="nav-link text-white" to="/careers">Careers</NavLink>
        </li>
        <li className="nav-item">
        <NavLink className="nav-link text-white" to="/notfound">NotFound</NavLink>
        </li>
        <li className="nav-item">
       <NavLink className="nav-link text-white" to="/products">Products</NavLink>
        </li>
       
      </ul>
      <form className="d-flex">
        <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search"/>
        <button className="btn btn-outline-success" type="submit">Search</button>
      </form>
    </div>
  </div>
</nav>
  </>;
}
